import React, {useEffect, useState} from 'react';
import axios from 'axios';
import './promo.css';


export default function Promotion() {
  const [promotions, setPromotions] = useState([]);

  useEffect(() => {
    function loadPromotions(){
      const user = JSON.parse(localStorage.getItem('user'));
      const id = JSON.parse(localStorage.getItem('tenantid'));
      // const id = parseInt(localStorage.getItem('tenantid'))
      const token = JSON.parse(localStorage.getItem('token'));
      console.log("the user token is", token)
      console.log("this is the user id", id)
      console.log("promotion user", user)

      const headers = {
              Authorization: 'Bearer ' + token
          }
          // console.log("token is: " + user.token);
      axios.get(`http://127.0.0.1:8000/api/promotions/${id}`, {headers: headers})
      .then(res => {
          console.log("this is axios response", res)
          const allpromos = res.data;
          console.log("these are the running promotions", allpromos)
          localStorage.setItem('allPromotions', JSON.stringify(res.data))
          setPromotions(allpromos);
          console.log(promotions);
      })
      .catch(err => {
          console.log(err)
      })   
  }
  loadPromotions(); 
  }, []);


  return (
    <div className="wrapper-promo">
        {promotions.map((promotion, index) => (
          <div className="promotionContainer col-md-12" key={promotion.id}>
          <div className="img-container">
            <img className="img-fluid img-thumbnail promo-img" src={`http://www.tendaafrica.com/cfms/dashboard/cfms/public/${promotion.image}`} alt="promo-cover" />
          </div>
          <div className="promo-details">
            <div className="promo-title">
              <h6>{promotion.title}</h6>
            </div>
            <div className="promo-desc">
              <p>
               {promotion.description}
              </p>
            </div>
          </div>
        </div>
        ))}
    </div>
  )
}
