import React, {useState, useRef, useEffect} from 'react';
import {Form, Button, Container} from 'react-bootstrap';
import { useNavigate } from "react-router-dom"
import axios from 'axios';

import './loginStyles.css';

function Login(props) {
    const errRef = useRef();

    const [errMsg, setErrMsg] = useState('');

    const [formInput, setformInput] = useState({email:'', password:''});

    const updateFormInput = e => {
        e.persist();

        setformInput(prevState => ({...prevState, [e.target.name]: e.target.value}))
    }
    
    let navigate = useNavigate();

    useEffect(() => {
      setErrMsg('')
    }, [formInput]);
  

    const signIn = async (e) => {
        e.preventDefault();
        axios.get('http://www.tendaafrica.com/cfms/dashboard/cfms/public/sanctum/csrf-cookie').then(()=>{
          axios.post('http://www.tendaafrica.com/cfms/dashboard/cfms/public/api/auth/login', formInput)
          .then((response) => {
            // console.log('login success');
            // console.log(response.data);
            localStorage.setItem('user', JSON.stringify(response.data))
            localStorage.setItem('token', JSON.stringify(response.data.token))
            localStorage.setItem('id', JSON.stringify(response.data.user.id))
            navigate('/tenant')
          })
          .catch((error) => {
            console.log(error)
            if(error.response){
              console.log(error.message)
              setErrMsg('invalid username or password')
              errRef.focus();
            } else if(error.request){
              console.log(error.request)
              setErrMsg('server unreachable ')

            } else{
              console.log(error.message)
            }
          })

        }).catch((err) => {
          console.log(err)
        })
  }
    
  return (
    <Container fluid className="loginContainer">
        <Container className="loginWrapper col-12 col-sm-12 col-md-12 col-xs-12">
            <Form className="loginForm col-12 col-sm-12 col-md-12 col-xs-12" onSubmit={signIn}>
              <div className="loginHead">
                <p ref={errRef} className="errorMessage" aria-live="assertive">{errMsg}</p>
                <h4>Please Login</h4>
                <p>to view all your outlets</p>
              </div>

                <Form.Group className="mb-3" controlId="formBasicEmail">
                    <Form.Label>Email address</Form.Label>
                    <Form.Control type="email" name="email" placeholder="Email" required onChange={updateFormInput}/>
                </Form.Group>

                <Form.Group className="mb-3" controlId="formBasicPassword">
                    <Form.Label>Password</Form.Label>
                    <Form.Control type="password" name="password" placeholder='Password' required onChange={updateFormInput} />
                </Form.Group>
               <div className="d-grid">
               <Button className="loginButton"  type='submit' onClick={signIn}>
                    Login
                </Button>
               </div>
            </Form>
        </Container>
    </Container>
  )
}

export default Login