import React, {useState, useEffect} from 'react';
import axios from 'axios';
import { Form, Button } from 'react-bootstrap'
import { useNavigate } from 'react-router-dom';
import './tenantselect.css';

function Tenant() {
    const [tenants, setTenants] = useState([]);
    const [value, setValue] = useState();
    const [selected, setSelected] = useState();


    let navigate = useNavigate();

    useEffect(() => {
        function loadTenants(){
            const user = JSON.parse(localStorage.getItem('user'));
            // console.log(user)

            const headers = {
                    Authorization: 'Bearer ' + user.token
                }
                // console.log("token is: " + user.token);
            axios.get("http://www.tendaafrica.com/cfms/dashboard/cfms/public/api/tenants", {headers: headers})
            .then(res => {
                const tenants = res.data;
                console.log("these are the tenants", tenants)
                localStorage.setItem('allTenants', JSON.stringify(res.data))
                setTenants(tenants)
            })
            .catch(err => {
                console.log(err)
            })
            
        }
        loadTenants();
    }, [])

    function handleOnChange(e){
        // const allTenants = JSON.parse(localStorage.getItem('allTenants'));
        // setValue(allTenants)
        // console.log("press proceed")
        // console.log(event.target.id);

        // setSelected(tenants[0].value);
        console.log(e.target.value);
        const tenantid=e.target.value;
        // setValue(e.target.value)
        localStorage.setItem('tenantid', JSON.stringify(tenantid))
        // navigate(`/main`)

    }
    function handleSubmit(tenant){

        navigate(`/main`)
    }
  return (
    <Form onSubmit={handleSubmit}>
        <Form.Select id="selectForm" onChange={handleOnChange}>
            {tenants.map(tenant => (<option key={tenant.id} value={tenant.id} >{tenant.name}</option>))}
        </Form.Select>
        
       <div className="d-grid">
        <Button className="tenantSelectbutton" type='submit' >
                Proceed
            </Button>
       </div>
    </Form>
    
  )
}

export default Tenant